import { Card, Grid, styled, useTheme, Box, Button, Menu, MenuItem, LinearProgress, TextField, Icon, CircularProgress } from '@mui/material';
import { Fragment, useEffect, useState, useRef } from 'react';
import Campaigns from './shared/Campaigns';
import DoughnutChart from './shared/Doughnut';
import RowCards from './shared/RowCards';
import StatCards from './shared/StatCards';
import StatCards2 from './shared/StatCards2';
import TopSellingTable from './shared/TopSellingTable';
import UpgradeCard from './shared/UpgradeCard';
import SimpleTable from "./SimpleTable";
import NumberConsole from 'app/components/NumberConsole'
import useSettings from 'app/hooks/useSettings';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useSearchParams } from "react-router-dom";
import Parse from 'app/vendor/Parse'
import { useDispatch, useSelector } from 'react-redux';
import KeyPad from 'app/components/KeyPad'
import Search from 'app/components/Search'
import Filter from "app/components/Filter"
import { setApplicationMessage } from 'app/redux/actions/ApplicationActions'
import { NumberCategory } from 'app/../../shared/NumberCategory';
import ValueComponent from 'app/components/ValueComponent'



const chunk = (arr, len) => {

    var chunks = [],
        i = 0,
        n = arr.length;

    while (i < n) {
        chunks.push(arr.slice(i, i += len));
    }

    return chunks;
}
const ITEM_KEY = 'numbers'

const Component = (props) => {
    const dispatch = useDispatch()
    const [data, setData] = useState([])
    const [value, setValue] = useState("");
    const [values, setValues] = useState([])
    const canvasRef = useRef(null)
    const [query, setQuery] = useState('')
    const [queriedDate, setQueriedData] = useState([])
    const filterList = [
        {
            name: '3rd SG Number',
            itemKey: 'sg3rdpartynumbers'
        },
        {
            name: '3rd MY Number',
            itemKey: 'my3rdpartynumbers'
        },
        {
            name: 'SG Number',
            itemKey: 'sgnumbers'
        },
        {
            name: 'My Number',
            itemKey: 'mynumbers'
        },

    ]
    const [selectedIndex, setSelectedIndex] = useState(0)
    const [isLoading, setIsLoading] = useState(false)

    useEffect(() => {
        setData([])
        search((''))
    }, [selectedIndex])


    const search = async (query) => {
        setIsLoading(true)
        const response = await Parse.Cloud.run('searchNumbers', {
            query,
            itemKey: filterList[selectedIndex].itemKey
        })
        if (response?.message) {
            dispatch(setApplicationMessage({
                text: response?.message
            }))
        } else {
            setData(response)
        }
        setIsLoading(false)
    }

    useEffect(() => {
        const canvas = canvasRef.current
        const context = canvas.getContext('2d')
        //Our first draw

        context.font = "30px Arial";
        context.fillStyle = '#000000'
        const title = "Mobile Number For Sale"

        context.fillRect(0, 0, context.canvas.width, context.canvas.height)


        context.fillStyle = '#ffffff'
        context.fillText(title, 50, 50)
        chunk(values, 1).map((arr, e) => {
            arr.forEach((nn, ii) => {
                context.fillText(nn, ii * 150 + 50, e * 60 + 150)
            })

        })

        context.fillText(value, 50, 550)

    }, [values, value])

    useEffect(() => {
        const newData = data
            .filter(e => e.num.includes(query))
        setQueriedData(newData)
    }, [data, query])

    return <>
        <Box display={"flex"} justifyContent={"center"} alignItems={"center"}>

            <Search
                onSearch={(query) => { }}
                onQuery={q => { setQuery(q) }}
                isLoading={isLoading}
                data={queriedDate}
                headline1={"Reseller Tool"}
                startContent={<Box display={"flex"} justifyContent={"center"} alignItems={"center"}><Filter
                    handleMenuItemClick={(e, index, item) => { setSelectedIndex(index) }}
                    selectedIndex={selectedIndex}
                    list={filterList}
                /></Box>}
                handleItemClick={(n) => {

                    if (!values.includes(n)) {
                        setValues([
                            ...values,
                            n.num
                        ])
                    }
                }}
                displaySource={false}
                pageSize={Number(10)}
                hideCopy={true}
                hideDl={true}
                mask={'yyyyyyyy'}
                crazySale={false}
                min={0}
                max={9999}
                hideButton={true}
            />
        </Box>
        <Box display={"flex"} justifyContent={"center"} alignItems={"center"} flexDirection={"column"}>


            <TextField

                onChange={e => setValue(e.target.value)}
                value={value}

            />
            <canvas height="600" width="600" ref={canvasRef} {...props} />
        </Box>

    </>
};

export default Component;
